.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #6d778c;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.workpace{
  background: #1c2e46;
}
.custom-node{
  padding: 10px;
}
.makeStyles-search-4{
  width: 60% !important;
  margin-left: 30px !important;
}
.title{
  font-size: 10px;
  font-weight: bold;
  color: grey;
  padding-left: 10px;
  padding-right: 10px;
}
.description{
  font-size: 6px;
  color: grey;
  padding-left: 10px;
  padding-right: 10px;
}
.container{
  max-width: 100% !important;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
